.glitch {
  position: relative;
  color: white;
  animation: glitch-skew 1s infinite linear alternate-reverse;
}

.glitch::before {
  content: attr(data-text);
  position: absolute;
  top: 0;
  left: 0;
  width: 150%;
  height: 100%;
  left: 2px;
  text-shadow: -2px 0 #ff00c1;
  clip: rect(44px, 450px, 56px, 0);
  animation: glitch-anim 5s infinite linear alternate-reverse;
}

.glitch::after {
  content: attr(data-text);
  position: absolute;
  top: 0;
  left: 0;
  width: 150%;
  height: 100%;
  left: -2px;
  text-shadow: -2px 0 #00fff9, 2px 2px #ff00c1;
  animation: glitch-anim2 1s infinite linear alternate-reverse;
}

@keyframes glitch-anim {
  0% {
    clip: rect(48px, 9999px, 5px, 0);
    transform: skew(0.52deg);
  }

  5% {
    clip: rect(12px, 9999px, 50px, 0);
    transform: skew(0.86deg);
  }

  10% {
    clip: rect(30px, 9999px, 22px, 0);
    transform: skew(0.65deg);
  }

  15% {
    clip: rect(47px, 9999px, 93px, 0);
    transform: skew(0.59deg);
  }

  20% {
    clip: rect(59px, 9999px, 8px, 0);
    transform: skew(0.11deg);
  }

  25% {
    clip: rect(48px, 9999px, 20px, 0);
    transform: skew(0.25deg);
  }

  30% {
    clip: rect(1px, 9999px, 28px, 0);
    transform: skew(0.5deg);
  }

  35% {
    clip: rect(55px, 9999px, 11px, 0);
    transform: skew(0.51deg);
  }

  40% {
    clip: rect(19px, 9999px, 16px, 0);
    transform: skew(0.77deg);
  }

