@media (max-width: 1200px) {
  .grid-homepage {
    display: grid;
    grid-gap: 16px;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin-bottom: 16px;
  }

  .box-about {
    grid-column: 1 / 3;
    grid-row: 1 / 3;
  }

  .box-releases {
    grid-column: 3;
    grid-row: 1;
  }

  .box-links {
    grid-column: 4;
    grid-row: 1;
  }

  .box-said {
    grid-column: 1 / 4;
    grid-row: 4;
  }

  .box-contact {
    grid-column: 4;
    grid-row: 4;
  }

  .box-blog {
    grid-column: 3 / 5;
    grid-row: 2;
  }

  .box-artwork {
    grid-column: 1 / 3;
    grid-row: 3;
  }

  .box-mods {
